<template>
    <el-container>
        <el-header>
            <container-top></container-top>
        </el-header>
        <el-container>
            <nav-header></nav-header>
            <el-main>
                <el-container class="container-main-div">
                    <div class="container-div">
                        <div class="container-main">
                            <div class="top-main">
                                <div class="page-top-line">
                                    <div class="page-title">节点设置</div>
                                </div>
                            </div>
                            <div class="out-div">
                                <div class="nodeset-list">
                                    <template>
                                        <el-form ref="form" :rules="rulesEdit" :model="form" label-width="120px" style="width: 400px;text-align: center;">
                                            <el-form-item label="节点名称" prop="nodeName">
                                                <el-input v-model="form.nodeName"></el-input>
                                            </el-form-item>
                                            <el-form-item label="机构logo" style="width: 400px;">
                                                <img id="base64Img" :src="headImg"/>
                                                <div class="img-div">
                                                    <p>选择图片</p>
                                                    <el-input v-model="form.logo" class="add-uploadimg" type="file" @change="imgChange" style="opacity: 0;"></el-input>
                                                </div>
                                            </el-form-item>
                                            <el-form-item label="IP地址" prop="ipAddress">
                                                <el-input v-model="form.ipAddress"></el-input>
                                            </el-form-item>
                                            <el-form-item label="端口号" prop="port">
                                                <el-input v-model="form.port"></el-input>
                                            </el-form-item>
                                            <el-form-item label="CSV文件路径" prop="CSV_path">
                                                <el-input v-model="form.CSV_path"></el-input>
                                            </el-form-item>
                                            <el-button class="submit-btn-style" @click="submitDataset">重新设置</el-button>
                                        </el-form>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-container>
            </el-main>
        </el-container>
    </el-container>
</template>
<style>
    @import "../../static/css/nodeSet.css";
</style>
<script>
import $ from "jquery";
import Header from "../sub/aside";
import containerTop from "../sub/container_top";
export default {
    name: "nodeSet",
    components: {
        "nav-header": Header,
        "container-top": containerTop,
    },
    data: function() {
        return {
            username:Cookies.get("fedname"),
            token:Cookies.get("fedtoken"),
            form:{
                nodeName:'',
                ipAddress:'',
                port:'',
                CSV_path:'',
                logo:'',
            },
            oldName:'',
            headImg:'',
            rulesEdit:{
                nodeName:[{ required: true, message: '请填写节点名称', trigger: 'blur' }],
                ipAddress:[{ required: true, message: '请填写IP地址', trigger: 'blur' }],
                port:[{ required: true, message: '请填写端口号', trigger: 'blur' }],
                CSV_path:[{ required: true, message: '请填写CSV文件路径', trigger: 'blur' }],
            }
        }
    },
    mounted(){
        this.findDimensions()
        this.getNodeInfo()
    },
    methods: {
        // 节点信息
        getNodeInfo:function(){
            var that = this;
            that.axios.post('/operatorInfoReq',that.qs.stringify({
                "operator":that.username,
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.form.nodeName=response.data.nodeName;
                    that.form.ipAddress=response.data.ipAddress;
                    that.form.port=response.data.port;
                    that.form.CSV_path=response.data.CSV_path;
                    that.headImg=response.data.logo;
                    that.oldName=response.data.nodeName;
                    if(response.data.logo){
                        $("#base64Img").show();
                    }
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
        imgChange:function(){
            var that=this;
            var file =  document.querySelector('.add-uploadimg input[type=file]').files[0];
            console.log(file)
            var reader = new FileReader();
            reader.onloadend = function () {
                $("#base64Img").show();
                that.headImg=reader.result;
            }
            if (file) {
                reader.readAsDataURL(file);
            }
        },
        submitDataset:function(){
            var that=this;
            that.axios.post('/nodeModify',that.qs.stringify({
                'old_nodeName':that.oldName,
                'nodeName':that.form.nodeName,
                'ipAddress':that.form.ipAddress||'',
                'port':that.form.port||'',
                'CSV_path':that.form.CSV_path||'',
                'logo':that.headImg||'',
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.$message({
                        message: '修改成功',
                        type: 'success'
                    });
                    that.currentPage=1;
                    that.getNodeInfo()
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
        findDimensions:function(){
            if(window.innerWidth)
            this.winWidth = window.innerWidth;
            else if ((document.body) && (document.body.clientWidth))
            this.winWidth = document.body.clientWidth;
            //获取窗口高度
            if(window.innerHeight)
            this.winHeight = window.innerHeight;
            else if((document.body) && (document.body.clientHeight))
            this.winHeight = document.body.clientHeight;
            //通过深入Document内部对body进行检测，获取窗口大小
            if(document.documentElement && document.documentElement.clientHeight && document.documentElement.clientWidth){
                this.winHeight = document.documentElement.clientHeight;
                this.winWidth = document.documentElement.clientWidth;
            }
            $(".nodeset-list").attr("style","min-height:"+(this.winHeight-290)+"px;")
        },
    }
};
</script>



